<template>
  <div>
    <div class="text-left" style="padding:20px">
      <v-btn x-small color="primary" @click="clickImport('collections')" >
        Import&nbsp;<v-icon small>mdi-import</v-icon>
      </v-btn>
      <v-btn x-small @click="() => clickJsonExport(collections)" color="#009688">
        Export &nbsp;<v-icon small>mdi-export</v-icon>
      </v-btn>
      <v-btn x-small @click="showVariableDialog" color="success">
        Variables&nbsp;<v-icon small>mdi-variable</v-icon>
      </v-btn>
      <v-btn @click="sendAll" x-small color="warning">
        Send&nbsp;<v-icon small>mdi-home-automation</v-icon>
      </v-btn>
      <v-btn x-small @click="saveAll" color="info">
        Save All
      </v-btn>
    </div>

    <v-tabs v-model="collectionTab" vertical>
      <draggable v-model="collections" @start="drag=true" @end="onDropCollection">
        <v-tab :key="index" v-for="(collection, index) of collections">
          {{collection.name}}
          <v-btn @click.native="removeCollection(index)" class="btn-delete" icon x-small color="warning">
            <v-icon x-small>mdi-close-circle</v-icon>
          </v-btn>
          <v-btn @click.native="(evt) => showCollectionSetting(index, evt)" class="btn-edit" icon x-small color="success">
            <v-icon x-small>mdi-square-edit-outline</v-icon>
          </v-btn>
        </v-tab>
        <v-tab :key="collections.length" class="api-tab" @click.native="addCollection">
          <v-icon>mdi-plus</v-icon>
        </v-tab>
      </draggable>
      <v-tabs-items v-model="collectionTab" >
        <v-tab-item :key="collectionIndex" v-for="(collection, collectionIndex) of collections">
          <v-card style="min-height: calc(100vh - 24px)">
            <div class="text-right" style="padding:20px">
              <v-btn x-small color="primary" @click="clickImport('items', collectionIndex)" >
                Import&nbsp;<v-icon small>mdi-import</v-icon>
              </v-btn>
              <v-btn x-small @click="() => clickJsonExport(collection.items)" color="#009688">
                Export &nbsp;<v-icon small>mdi-export</v-icon>
              </v-btn>
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn v-bind="attrs" v-on="on" x-small @click="sendAllInCollection(collectionIndex)" color="warning">
                    Send&nbsp;<v-icon small>mdi-home-automation</v-icon>
                  </v-btn>
                </template>
                <span>Send all request in this collection</span>
              </v-tooltip>
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn v-bind="attrs" v-on="on" x-small @click="() => saveData(collections[collectionIndex])" color="info">
                    Save
                  </v-btn>
                </template>
                <span>Save this collection</span>
              </v-tooltip>

            </div>
            <v-card-text>
              <v-tabs v-model="tab">
                <draggable v-model="collection.items" @start="drag=true" @end="() => onEnd(collectionIndex)">
                  <v-tab :style="{color: item.enable ? '#1976d2': 'gray'}" class="api-tab" :key="index" v-for="(item, index) of collection.items">
                    {{item.name}} &nbsp;
                    <v-btn @click.native="(event) => removeItem(collectionIndex, index, event)" class="btn-delete" icon x-small color="warning">
                      <v-icon x-small>mdi-close-circle</v-icon>
                    </v-btn>
                    <v-btn @click.native="(evt) => showTabSetting(collectionIndex, index, evt)" class="btn-edit" icon x-small color="success">
                      <v-icon x-small>mdi-square-edit-outline</v-icon>
                    </v-btn>
                  </v-tab>
                  <v-tab class="api-tab" @click.native="addItem(collectionIndex)" :key="collection.items.length">
                    <v-icon>mdi-plus</v-icon>
                  </v-tab>
                </draggable>
              </v-tabs>
              <v-tabs-items v-model="tab" >
                <v-tab-item :key="index" v-for="(item, index) of collection.items">
                  <v-card flat>
                    <v-card-text>
                      <api-client-tab
                        :send="(item) => sendRequest(collectionIndex, index)"
                        :save="(item) => onClickSave(item, collectionIndex, index)"
                        :resp="resps[collectionIndex] ? resps[collectionIndex][index]: ''"
                        :item="item" >
                      </api-client-tab>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
              </v-tabs-items>
            </v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
    </v-tabs>

    <v-dialog v-model="importDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>

        <v-card-text>
          <v-alert
            class="text-left"
            dense
            colored-border
            type="error"
            elevation="2"
            v-show="disableSave && dataImport"
          >Incorrect Data Type!</v-alert>
          <v-container>
            <v-textarea
              outlined
              name="input-7-4"
              label="import data"
              v-model="dataImport"
              @input="onChangeImportData"
            ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="collectionDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Collection Setting</span>
        </v-card-title>

        <v-card-text>
          <v-text-field
            v-if="collections[selectedCollectionIndex]"
            placeholder="Name"
            v-model="collections[selectedCollectionIndex].name"
            label="Name"
          >
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" text v-on:click="saveAll">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="variablesDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Global Variables</span>
        </v-card-title>

        <v-card-text>
          <key-value-form :items="variables"></key-value-form>
        </v-card-text>saveAll
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" text v-on:click="saveVariables">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="tabDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Tab Setting</span>
        </v-card-title>

        <v-card-text>
          <v-text-field
            placeholder="Name"
            v-if="collections[selectedCollectionIndex] && collections[selectedCollectionIndex].items[selectedTabIndex]"
            v-model="collections[selectedCollectionIndex].items[selectedTabIndex].name"
            label="Name"
          >
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" text v-on:click="() => saveData(collections[selectedCollectionIndex])">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import ApiClientTab from './ApiClientTab';
import KeyValueForm from './KeyValueForm.vue';
import draggable from 'vuedraggable'
import { copyToClipboard, isJsonString } from '../utils/utils';
import axios from '../axios';
import _axios from 'axios';

const defaultItem = {name: 'Test', method: 'Get', params: [], headers: [], body: [], enable: true};

export default {
    components: {
        ApiClientTab,
        KeyValueForm,
        draggable
    },
    data: () => ({
        variablesDialog: false,
        importDialog: false,
        collectionDialog: false,
        tabDialog: false,
        disableSave: true,
        variables: [],
        dataImport:"",
        selectedCollectionIndex: 0,
        selectedTabIndex: 0,
        tab: 0,
        collectionTab: 0,
        importType: '',
        resps: [],
        collections: [{name: 'test', items: [defaultItem]}],
        defaultCollections: [{name: 'test', items: [defaultItem]}],
    }),
    mounted() {
        this.getData();
        const { setting } = this.$store.state;
        this.variables = setting.variables || [];
    },
    methods: {
        async sendAll() {
            for (let i = 0; i< this.collections.length; i++) {
                await this.sendAllInCollection(i)
            }
        },
        showCollectionSetting(index, evt) {
            this.selectedCollectionIndex = index;
            this.collectionDialog = true;
            evt.stopPropagation();
        },
        showTabSetting(collectionIndex, tabIndex, evt) {
            this.selectedCollectionIndex = collectionIndex;
            this.selectedTabIndex = tabIndex;
            this.tabDialog = true;
            evt.stopPropagation();
        },
        async onDropCollection(){
            this.drag = false;
            let index = 0;
            for (const collection of this.collections) {
                collection.index = index;
                await this.saveData(collection);
                index++;
            }
        },
        async onEnd(collectionIndex){
          this.drag = false;
          let index = 0;
          for (const item of this.collections[collectionIndex].items) {
            item.index = index;
            index++;
          }
          await this.saveData(this.collections[collectionIndex]);
        },
        async saveAll() {
          for (const collection of this.collections) {
            await this.saveData(collection);
          }
          this.getData();
        },
        async sendAllInCollection(collectionIndex) {
            const { items } = this.collections[collectionIndex];
            let index = 0;
            this.$store.commit('changeLoading', true);
            this.resps[collectionIndex] = [];
            for (const item of items) {
              if (!item.enable || !item.url) {
                continue;
              }
              try {
                  const data = await this.send(item, index);
                  this.resps[collectionIndex][index] = data;
                  this.resps = [...this.resps];
                  this.$toasted.show(`${item.name} success!`);
              } catch(err) {
                  this.resps[collectionIndex][index] = err;
                  this.resps = [...this.resps];
                  this.$toasted.show(`${item.name} failed!`, {type: 'error'});
              }
              index++;
            }
          this.$store.commit('changeLoading', false);
        },
        sendRequest(collectionIndex, index) {
          const item = this.collections[collectionIndex].items[index];
          this.$store.commit('changeLoading', true);
          if (!this.resps[collectionIndex]) {
              this.resps[collectionIndex] = [];
          }
          this.send(item, index).then((data) => {
              this.resps[collectionIndex][index] = data;
              this.resps = [...this.resps];
              this.$store.commit('changeLoading', false);
          }).catch((err) => {
              const { response } = err;
              this.$store.commit('changeLoading', false);
              this.resps[collectionIndex][index] = err;
              this.resps = [...this.resps];
          });
            this.resps = [...this.resps];
        },
        send(item) {
          const { variables } = this.$store.state.setting;
          if (variables) {
              for (const variable of variables) {
                  if (variable.key && variable.value && variable.enable) {
                      window[variable.key] = variable.value;
                  }
              }
          }

          const headers = {};
          for (const header of item.headers) {
            if (header.key && header.value && header.enable) {
              headers[eval('`' + header.key + '`')] = eval('`' + header.value + '`');
            }
          }

          const body = {};
          for (const bodyItem of item.body) {
            if (bodyItem.key && bodyItem.value && bodyItem.enable) {
                let value = bodyItem.value;
                if (bodyItem.type === 'boolean') {
                    value = value === "true";
                }
                if (bodyItem.type === 'string') {
                    value = eval('`' + value + '`');
                }
                if (bodyItem.type === 'number') {
                    value = Number(value);
                }
                body[eval('`' + bodyItem.key + '`')] = value;
            }
          }
          const url = eval('`' + item.url + '`');
          const method = item.method.toLocaleLowerCase();

          const data = {
              url, method,
              headers, body
          };
          return axios.post('api-client/send', data);
        },
        async onClickSave(item, collectionIndex, itemIndex) {
            this.collections[collectionIndex].items[itemIndex] = item;
          const r = await this.saveData(this.collections[collectionIndex]);
          await this.getData();
          this.$toasted.show(r.status);
        },
        saveVariables() {
          const { setting } = this.$store.state;
          setting.variables = this.variables;

          this.$store.commit('changeLoading', true);
          axios.put(`setting`, setting).then(result => {
            this.$store.commit('changeLoading', false);
            if (result === 'successful') {
              this.$store.commit('changeSetting', setting);
              this.$toasted.show('Success!');
              this.variablesDialog = false;
            }
          });
        },
        showVariableDialog() {
          this.variablesDialog = true;
        },
        async onChangeImportData(){
          this.disableSave = !isJsonString(this.dataImport) || !Array.isArray(JSON.parse(this.dataImport));
        },
        clickJsonExport(data){
          const status = copyToClipboard(JSON.stringify(data));
          const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
          const type = status ? `success` : `error`;
          return this.$toasted.show(msg, {type: type});
        },
        clickImport(type, index) {
          this.importDialog = true;
          this.importType = type;
          if (type === 'items') {
              this.selectedCollectionIndex = index;
          }
        },
        async saveImport() {
            if (this.importType === 'items') {
                const items = JSON.parse(this.dataImport);
                this.collections[this.selectedCollectionIndex].items = [
                    ...this.collections[this.selectedCollectionIndex].items,
                    ...items
                ];
                this.saveData(this.collections[this.selectedCollectionIndex]);
            }
            if (this.importType === 'collections') {
                const collections = JSON.parse(this.dataImport);
                for (let i = 0; i < collections.length; i ++) {
                    const data = {...collections[i]};
                    delete data._id;
                    this.saveData(data);
                }
            }
          this.importDialog = false;
          this.getData();
        },
        closeDialog(){
          this.variablesDialog = false;
          this.importDialog = false;
          this.collectionDialog = false;
          this.tabDialog = false;
        },
        async removeItem(collectionIndex, index, event) {
            const collection = this.collections[collectionIndex];
            collection.items.splice(index,  1);
            this.saveData(collection);
            event.stopPropagation();
        },
        async removeCollection(index) {
            const collection = this.collections[index];
            if (collection._id) {
                await this.deleteData(collection._id);
            }
            this.collections.splice(index,  1);
        },
        async deleteData(id) {
          const r = await axios.delete(`api-client/${id}`)
          this.$toasted.show(r.status);
        },
        async saveData(data) {
            const r = await axios.put('api-client', data);
            this.closeDialog();
            return r;
        },
        addItem(index) {
          const item = JSON.parse(JSON.stringify(defaultItem))
          this.collections[index].items.push(item);
          this.tab = this.collections[index].items.length - 1;
        },
        addCollection() {
            const collection = JSON.parse(JSON.stringify(this.defaultCollections[0]));
            this.collections.push(collection);
            this.collectionTab = this.collections.length - 1;
        },
        async getData() {
          const collections = await axios.get('api-client');
          for (const collection of collections) {
            if (!collection.items) {
              collection.items = [defaultItem].slice();
            }
          }
          this.collections = collections.length === 0 ? [...this.defaultCollections]: collections;
        }
    }
};
</script>
<style>
  .btn-delete {
    position: absolute !important;
    right: 2px;
    top: 2px;
  }
  .btn-edit {
    position: absolute !important;
    right: 20px;
    top: 2px;
  }
  .api-tab {
    float: left;
    height: 100%;
    min-width: 200px !important;
  }
</style>
